import React from "react";
import { Card, CardBody, CardHeader } from "react-bootstrap";
import AssetsbySkeleton from "../../layout/skeleton/assetsbySkeleton";
import NoData from "../../layout/common/noData";
import { Link } from "react-router-dom";
import NetworkIcon from "../../layout/common/networkIcon";

const AssetsNetwork = (props) => {
  const { postbyNetwork, loader } = props;

  return (
    <Card className="sw-card-custom gutter-b card-shadow mb-5">
      <CardHeader className="p-5 d-flex align-items-center justify-content-between">
        <h6 class="card-label font-weight-bolder m-0">
          Top Networks
        </h6>
      </CardHeader>
      <CardBody className="p-5">
        <div className="mx-height-345">
        { loader ? <AssetsbySkeleton/> : postbyNetwork && Object.keys(postbyNetwork).length
          ? postbyNetwork.map((data) => {
              return (
              // <Link className="mb-4 last-child-mb-0 d-block" to={`/moderation/index/${localStorage.getItem('wallId')}?network=${data.feedId}`}> 
                <div className="px-5 py-2 border rounded">
                  <div className="d-flex align-items-center justify-content-between">
                    <div className="d-flex align-items-center me-10">
                      <div className="rounded me-3 d-flex">
                        <NetworkIcon icon={data.icon} size={'font-size-h2'}/>
                      </div>
                      <div className="">
                        <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name">
                          {data.name}
                        </p>
                      </div>
                    </div>
                    <p className="text-dark count-tag border rounded font-weight-boldest m-0">
                      {data.Posts}
                    </p>
                  </div>
                </div>
              //  </Link> 
              );
            })
          : <NoData/>}
         </div> 
      </CardBody>
    </Card>
  );
};

export default React.memo(AssetsNetwork);
